import React, {Component} from 'react';
import s from './styles.module.scss'
import CardForBasket from '../CardForBasket/CardForBasket'

class Basket extends Component {
    constructor() {
        super()
        this.state = {
            cards: []
        }
    }

    componentDidMount() {
        const contentful = require('contentful')

        const client = contentful.createClient({
        space: '3afxhmr1xm65',
        accessToken: 'c8cdb5066fb665984fa30eb15c75d2eb694e461647a9db057dbd27957647de8d'
        })

        client.getEntries({
        content_type: 'basket'
        })
            .then((response) => {
                this.setState({
                    cards: response.items
                })
            })
            .catch(console.error)

    }


    render() {
        const {data} = this.props
        const cards = this.state.cards.map((card, i) => <CardForBasket id={i} key={i} data={card}/>)
        return (
            <div className={s.container}>
                <h3 className={s.title}></h3>
                <div className={s.catalogWrapper}>
                    {cards}
                </div>
                <div className={s.totalPrice}>  : {this.state.price} </div>
            </div>
        )
    }
}


export default Basket;



